.mdl-dialog {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	z-index: $ls-dialog-z-index;
	padding: 20px;
	box-sizing: border-box;
	visibility: hidden;

	&.mdl-dialog--show {
		visibility: visible;
	}

	&--no-padding {

		.mdl-dialog__content {
			padding: 0;		
		}

		.mdl-dialog__header {
			padding: $ls-dialog-padding;
			padding-bottom: 0;
		}

		.mdl-dialog__footer {
			padding: $ls-dialog-padding;
			padding-top: 10px;
		}
	}
}

.mdl-dialog__content {
	box-sizing: border-box;
	padding: $ls-dialog-padding;
	box-shadow: $ls-dialog-shadow;
	border: $ls-dialog-border;
	max-width: $ls-dialog-max-width;
	background-color: $ls-color-white;
	transform: scale(0.7);
	opacity: 0;
	transition: all 0.3s;
	max-height: 100%;
	display: flex;
	flex-direction: column;

	.mdl-dialog.mdl-dialog--show & {
		transform: scale(1);
		opacity: 1;
	}
}

.mdl-dialog__header {
	flex-shrink: 0;
}

.mdl-dialog__header-title {
	display: block;
	font-size: $ls-dialog-title-font-size;
	font-weight: $ls-dialog-title-font-weight;
	line-height: $ls-dialog-title-line-height;
	padding-bottom: 19px;
}

.mdl-dialog__body {
	flex-grow: 1;
  flex-shrink: 1;
  overflow: auto;
  -webkit-overflow-scrolling: touch;

  &.mdl-dialog__body--full {
	margin-left : -(nth($ls-dialog-padding, 1));
	margin-right : -(nth($ls-dialog-padding, 1));
  }

	p {
		font-size: $ls-dialog-body-font-size;
		font-weight: $ls-dialog-body-font-weight;
	}
}

.mdl-dialog__footer {
	flex-shrink: 0;
	padding-top: 10px;
	text-align: right;
}